<template>
  <page-layout :title="title">
    <template slot="action">
      <div class="interval-sovle">
        <a-button-group style="margin-right: 8px">
          <a-button
            icon="export"
            @click="showExportModal"
          >
            导出
          </a-button>
        </a-button-group>
        <a-button
          type="primary"
          icon="search"
          @click="showQueryModel"
        >
          <span>查询</span>
          <queryModal ref="dialogtreeQueryModal"></queryModal>
        </a-button>
      </div>
    </template>
    <div class="comprehensiveAnalysis">
      <!-- <span style="margin-left: 8px">
        <template v-if="hasSelected">
          {{`Selected ${selectedRowKeys.length} items`}}
        </template>
      </span> -->
      <a-table
        :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
        :dataSource="dataSource"
        :columns="columns"
        @change="handleChange"
        :scroll="{ x: 1300 }"
        :pagination="pagination"
        :loading="loading"
      >
        <template
          slot="operation"
          slot-scope="text, record"
        >
          <a
            href="javascript:;"
            @click="onDelete(record.key)"
          >踢除</a>
        </template>
      </a-table>
    </div>
  </page-layout>
</template>

<script>
import ATable from "ant-design-vue/lib/table";
import AButton from "ant-design-vue/lib/button";
import { setTimeout, setImmediate } from "timers";
import queryModal from "./QueryModal";
import PageLayout from "@/components/Page/PageLayout";
const AButtonGroup = AButton.Group
export default {
  props: {
    customerId: String
  },
  components: {
    ATable,
    AButton,
    AButtonGroup,
    queryModal,
    PageLayout
  },
  data() {
    return {
      dataSource: [],
      selectedRowKeys: [],
      loading: false,
      loadingCallHistory: false,
      filteredInfo: null,
      sortedInfo: null,
      pagination: {
        showQuickJumper: true,
        pageSize: 10
      }
    };
  },
  mounted() {
    this.getDataFromServer();
  },
  watch: {
    customerId: {
      handler: function(customerId) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.deleteLocalHistory();
        }, 1000);
      }
    }
  },
  computed: {
    title() {
      return this.$route.meta.title;
    },
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
    columns() {
      let { sortedInfo, filteredInfo } = this;
      sortedInfo = sortedInfo || {};
      filteredInfo = filteredInfo || {};
      const columns = [
        {
          title: "登录名",
          dataIndex: "loginName",
          key: "loginName",
          sorter: (a, b) => a.loginName.length - b.loginName.length,
          sortOrder: sortedInfo.columnKey === "loginName" && sortedInfo.order
        },
        {
          title: "姓名",
          dataIndex: "seatName",
          key: "seatName",
          sorter: (a, b) => a.seatName.length - b.seatName.length,
          sortOrder: sortedInfo.columnKey === "seatName" && sortedInfo.order
        },
        {
          title: "工作组",
          dataIndex: "workTeam",
          key: "workTeam",
          sorter: (a, b) => a.workTeam.length - b.workTeam.length,
          sortOrder: sortedInfo.columnKey === "workTeam" && sortedInfo.order
        },
        {
          title: "总名单数",
          dataIndex: "listTotal",
          key: "listTotal",
          sorter: (a, b) => a.listTotal.length - b.listTotal.length,
          sortOrder: sortedInfo.columnKey === "listTotal" && sortedInfo.order
        },
        {
          title: "目标名单",
          dataIndex: "targetList",
          key: "targetList",
          sorter: (a, b) => a.targetList.length - b.targetList.length,
          sortOrder: sortedInfo.columnKey === "targetList" && sortedInfo.order
        },
        {
          title: "首拨完成",
          dataIndex: "firstDialFinished",
          key: "firstDialFinished",
          sorter: (a, b) =>
            a.firstDialFinished.length - b.firstDialFinished.length,
          sortOrder:
            sortedInfo.columnKey === "firstDialFinished" && sortedInfo.order
        },
        {
          title: "首拨进度",
          dataIndex: "firstDialProgress",
          key: "firstDialProgress",
          sorter: (a, b) =>
            a.firstDialProgress.length - b.firstDialProgress.length,
          sortOrder:
            sortedInfo.columnKey === "firstDialProgress" && sortedInfo.order
        },
        {
          title: "无效名单",
          dataIndex: "noEffectList",
          key: "noEffectList",
          sorter: (a, b) => a.noEffectList.length - b.noEffectList.length,
          sortOrder: sortedInfo.columnKey === "noEffectList" && sortedInfo.order
        },
        {
          title: "失败数",
          dataIndex: "failure",
          key: "failure",
          sorter: (a, b) => a.failure.length - b.failure.length,
          sortOrder: sortedInfo.columnKey === "failure" && sortedInfo.order
        },
        {
          title: "报价数",
          dataIndex: "quoteAmount",
          key: "quoteAmount",
          sorter: (a, b) => a.quoteAmount.length - b.quoteAmount.length,
          sortOrder: sortedInfo.columnKey === "quoteAmount" && sortedInfo.order
        },
        {
          title: "报价率",
          dataIndex: "quoteRatio",
          key: "quoteRatio",
          sorter: (a, b) => a.quoteRatio.length - b.quoteRatio.length,
          sortOrder: sortedInfo.columnKey === "quoteRatio" && sortedInfo.order
        },
        {
          title: "意向成功数",
          dataIndex: "intentionSuccess",
          key: "intentionSuccess",
          sorter: (a, b) =>
            a.intentionSuccess.length - b.intentionSuccess.length,
          sortOrder:
            sortedInfo.columnKey === "intentionSuccess" && sortedInfo.order
        },
        {
          title: "预约数",
          dataIndex: "appointmentAmount",
          key: "appointmentAmount",
          sorter: (a, b) =>
            a.appointmentAmount.length - b.appointmentAmount.length,
          sortOrder:
            sortedInfo.columnKey === "appointmentAmount" && sortedInfo.order
        },
        {
          title: "总预约数",
          dataIndex: "totalAppointmentAmount",
          key: "totalAppointmentAmount",
          sorter: (a, b) =>
            a.totalAppointmentAmount.length - b.totalAppointmentAmount.length,
          sortOrder:
            sortedInfo.columnKey === "totalAppointmentAmount" &&
            sortedInfo.order
        },
        {
          title: "总预占比",
          dataIndex: "appointmentRatio",
          key: "appointmentRatio",
          sorter: (a, b) =>
            a.appointmentRatio.length - b.appointmentRatio.length,
          sortOrder:
            sortedInfo.columnKey === "appointmentRatio" && sortedInfo.order
        },
        {
          title: "失败通序",
          dataIndex: "failureTX",
          key: "failureTX",
          sorter: (a, b) => a.failureTX.length - b.failureTX.length,
          sortOrder: sortedInfo.columnKey === "failureTX" && sortedInfo.order
        },
        {
          title: "总通序",
          dataIndex: "totalTX",
          key: "totalTX",
          sorter: (a, b) => a.totalTX.length - b.totalTX.length,
          sortOrder: sortedInfo.columnKey === "totalTX" && sortedInfo.order
        },
        {
          title: "成功通序",
          dataIndex: "successTX",
          key: "successTX",
          sorter: (a, b) => a.successTX.length - b.successTX.length,
          sortOrder: sortedInfo.columnKey === "successTX" && sortedInfo.order
        },
        {
          title: "续保数",
          dataIndex: "renewInsurance",
          key: "renewInsurance",
          sorter: (a, b) => a.renewInsurance.length - b.renewInsurance.length,
          sortOrder:
            sortedInfo.columnKey === "renewInsurance" && sortedInfo.order
        },
        {
          title: "孤儿单",
          dataIndex: "orphanBill",
          key: "orphanBill",
          sorter: (a, b) => a.orphanBill.length - b.orphanBill.length,
          sortOrder: sortedInfo.columnKey === "orphanBill" && sortedInfo.order
        }
      ];
      return columns;
    }
  },
  methods: {
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
    },
    showExportModal() {},
    getDataFromServer() {
      //TODO:通过AJAX请求获得呼叫历史信息（如果仅显示该坐席的与该客户的呼叫记录，那么参数参数为坐席id+客户id）
      for (let i = 0; i < 25; i++) {
        this.dataSource.push({
          key: i,
          id: i,
          loginName: `登录名-${i}`,
          seatName: `600${i}`,
          workTeam: `工作组-${i}`,
          listTotal: i,
          targetList: i,
          firstDialFinished: i,
          firstDialProgress: i,
          noEffectList: i,
          failure: i,
          quoteAmount: i,
          quoteRatio: i,
          intentionSuccess: i,
          appointmentAmount: i,
          totalAppointmentAmount: i,
          appointmentRatio: i,
          failureTX: i,
          totalTX: i,
          successTX: i,
          renewInsurance: i,
          successTX: i,
          orphanBill: i
        });
      }
    },
    deleteLocalHistory() {
      this.dataSource = [];
    },
    handleChange(pagination, filters, sorter) {
      this.filteredInfo = filters;
      this.sortedInfo = sorter;
    },
    onDelete(key) {
      let _this = this;
      this.$confirm({
        title: "你确定要踢除该坐席吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.loading = true;
          // ajax request after empty completing
          setTimeout(() => {
            _this.loading = false;
            const dataSource = [..._this.dataSource];
            _this.dataSource = dataSource.filter(item => item.key !== key);
          }, 1000);
        },
        onCancel() {
        }
      });
    },
    showQueryModel() {
      this.$refs.dialogtreeQueryModal.showQueryModal();
    }
  }
};
</script>

<style>
</style>



